/*----------Text colors------------*/
.darkBlueTx{
    color: $darkBlue;
}
.greenTx{
    color: $green;
}
.darkGrayTx{
    color: $darkGray;
}
.lightGrayTx{
    color: $lightGray;
}
.whiteTx{
    color:$white;
}
.blackTx{
    color: $black;
}

/*----------Text effects------------*/

.boldTx{
    font-weight: 700;
}
.semiBoldTx{
    font-weight: 500;
}
.lightTx{
    font-weight: lighter;
}
.cTx{
    text-align: center;
}
.rTx{
    text-align: right;
}
.lTx{
    text-align: left;
}
.jTx{
    text-align: justify;
}
.uppercase{
    text-transform: uppercase;
}
.lowercase{
    text-transform: lowercase;
}
.noDecorateTx{
    text-decoration: none;
}

